$iphone-chat-full-screen-max-width: 428px;
$iphone-chat-full-screen-max-height: 926px;

@mixin eureka-breakpoint-iphone($orientation: portrait) {
    @if $orientation == portrait {
        @include eureka-breakpoint-portrait {
            @media screen and (max-width: $iphone-chat-full-screen-max-width) and (max-height: $iphone-chat-full-screen-max-height) {
                @content;
            }
        }
    } @else {
        @include eureka-breakpoint-landscape {
            @media screen and (max-width: $iphone-chat-full-screen-max-height) and (max-height: $iphone-chat-full-screen-max-width) {
                @content;
            }
        }
    }
}

@mixin eureka-breakpoint-landscape() {
    @media (orientation: landscape) {
        @content;
    }
}

@mixin eureka-breakpoint-portrait() {
    @media (orientation: portrait) {
        @content;
    }
}
