@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/typography.scss';
@import '../../../styles/mixin/layout.scss';

.web-chat-window-container {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
    max-width: 375px;
    z-index: 999999;

    &__tile.eureka-tile {
        @include eureka-flex-column;
        z-index: 100;
        position: fixed;
        bottom: 0;
        width: 100%;
        max-width: 375px;
        box-shadow: 0 0 20px rgba(0, 0, 0, 40%);
        border-radius: var(--eureka-chat-window-border-radius);

        .eureka-tile__content-container {
            height: 100%;
            min-height: 290px;
        }
    }

    &__tile-header {
        @include eureka-flex-row;
        justify-content: space-between;
        width: 100%;
        align-items: center;

        &-title {
            @include eureka-h5;
            flex: 1 0 auto;
        }
    }

    &__title-btn-container--minimize-btn-container {
        margin-right: 16px;
    }

    .eureka-tile__heading-container {
        @include eureka-flex-row;
        height: 56px;
        border-bottom: none;
    }
}

@include eureka-breakpoint-iphone {
    .web-chat-window-container {
        max-width: $iphone-chat-full-screen-max-width;

        &__tile.eureka-tile {
            max-width: $iphone-chat-full-screen-max-width;
            height: 100%;
        }
    }
}

@include eureka-breakpoint-iphone(landscape) {
    .web-chat-window-container {
        max-width: $iphone-chat-full-screen-max-height;
        position: fixed;

        &__tile.eureka-tile {
            @include eureka-scroll-y;
            max-width: $iphone-chat-full-screen-max-height;
            height: 100%;
            max-height: $iphone-chat-full-screen-max-width;
        }
    }
}
