// ========================================================
// Dependencies
// ========================================================
@use 'sass:math';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/utils/map-utils.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/utils/unit-conversion-utils.scss';
@import '../tokens/color-light-theme-tokens.scss';
@import '../tokens/style-tokens.scss';

@mixin eureka-line-height($isHeading: false) {
    @if $isHeading {
        line-height: eureka-get-map-value($eureka-style-tokens, 'core-typography-headings-line-height');
    } @else {
        line-height: eureka-get-map-value($eureka-style-tokens, 'core-typography-line-height');
    }
}

@mixin eureka-max-line-width($lineheight: eureka-get-map-value($eureka-style-tokens, 'core-typography-line-height')) {
    // $div-form: 1em / #{$lineheight};
    $div-formula: math.div(1em, $lineheight); // font-size divided by lineheight
    max-width: calc(70 * #{$div-formula}); // max characters * (font-size / line-height)
}

@mixin eureka-heading-font-shared() {
    @include eureka-line-height(true);
    color: var(--eureka-core-color-headings-text, eureka-get-map-value($eureka-color-light-theme-tokens, 'core-color-headings-text'));
    font-family: var(--eureka-core-typography-headings-font-family, eureka-get-map-value($eureka-style-tokens, 'core-typography-headings-font-family')), var(--eureka-core-typography-headings-font-family-fallback, eureka-get-map-value($eureka-style-tokens, 'core-typography-headings-font-family-fallback'));
    font-style: var(--eureka-core-typography-headings-font-style, eureka-get-map-value($eureka-style-tokens, 'core-typography-headings-font-style'));
    font-weight: var(--eureka-core-typography-headings-font-weight, eureka-get-map-value($eureka-style-tokens, 'core-typography-headings-font-weight'));
    margin: 0;
}

@mixin eureka-number-font-shared() {
    font-weight: var(--eureka-core-typography-number-font-weight, eureka-get-map-value($eureka-style-tokens, 'core-typography-number-font-weight'));
}

@mixin eureka-body-font-shared() {
    font-family: var(--eureka-typography-body-font, eureka-get-map-value($eureka-style-tokens, 'core-typography-font-family'), eureka-get-map-value($eureka-style-tokens, 'core-typography-font-family-fallback'));
    line-height: eureka-get-map-value($eureka-style-tokens, 'core-typography-line-height');
}

@mixin eureka-h1() {
    @include eureka-heading-font-shared;
    @include eureka-max-line-width(eureka-get-map-value($eureka-style-tokens, 'core-typography-headings-line-height'));
    font-size: var(--eureka-core-typography-headings-level1-font-size, eureka-to-rem(eureka-get-map-value($eureka-style-tokens, 'core-typography-headings-level1-font-size')));
}

